.card {
    width: 50%;
    margin: auto auto 15% auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.cardImg {
    height: 100%;
}
.cardImg > img {
    height: 100%;
    width: 100%;
    vertical-align: middle;
    object-fit: cover;
}

.cardInfo {
    height: 100%;
    padding: 2em 2em 3em 2em;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-wrap: nowrap;
    overflow: auto;
}

.cardInfo::-webkit-scrollbar {
    width: 0;
}

.cardMataInfo {
    overflow: auto;
    display: grid;
    grid-template-columns: max-content 1fr;
    align-items: center;
    justify-content: center;
    align-content: center;
    grid-column-gap: 1.5em;
    grid-row-gap: .8em;
    width: fit-content;
}
.cardMataInfo::-webkit-scrollbar {
    width: 0;
}

.cardInfo > .name {
    font-size: 2em;
    margin-bottom: 1em;
}
.cardMataInfo .label {
    color: #979797;
    justify-self: end;
}
.cardMataInfo .value {
    margin: auto;
}
.cardMataInfo .episode {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center
}
.cardMataInfo .episode > div {
    margin: .2em;
}

a {
    color: var(--color-default);
}

.notFoundMsg {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.8em;
    color: #ccc;
    height: 5em;
    width: 15em;
    margin: 3em auto auto auto;
}

.goBack {
    padding: 1em;
    color: #7f7f7f;
    border-radius: .5em;
    background-color: rgba(0, 0, 0, 0.05);
    transition: all .5s;
    width: max-content;
    cursor: pointer;
    margin-left: 2em;
    position: absolute;
    user-select: none;
}

.goBack:hover {
    color: #535353;
    background-color: rgba(0, 0, 0, 0.25);
}

